#app {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#main-nav,
#console {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}
#main-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
  border-right: 1px solid #eee;
  font-weight: 300;
}
#main-nav h1 {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
  margin: 0;
  padding: 0 24px;
  font-size: 18px;
}
#main-nav ul {
  height: calc(100vh - 105px);
  margin: 0;
  padding: 0;
  overflow: auto;
}
#main-nav li a {
  display: block;
  padding: .5em 24px;
  text-decoration: none;
  color: #999;
  outline: none;
  line-height: 2;
}
#main-nav .router-link-active {
  color: #3685ff;
  background-color: #f6f9ff;
  font-weight: 600;
}
#main-nav footer {
  border-top: 1px solid #eee;
  color: #999;
  font-size: 12px;
  line-height: 45px;
  padding-left: 24px;
}
main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 240px;
  overflow: scroll;
  padding: 1em 4em 0;
  height: calc(100vh - 30px);
  transition: height .2s;
}
main h1 {
  border-bottom: 1px solid #eee;
  margin-bottom: 2em;
  padding-bottom: 1em;
  font-size: 18px;
}
.console-expanded main {
  height: calc(100vh - 40vh - 30px);
}
#console {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 240px;
}
.fa-icon {
  width: auto;
  height: 1em;
}



