/* Form widths */
.veui-form-demo {
  margin-bottom: 50px;
}
.veui-form-demo h2 {
  margin-bottom: 40px;
}
.veui-form-demo section + section {
  margin-top: 50px;
}
.veui-form-demo .veui-form[ui~="inline"] + .veui-form[ui~="inline"] {
  margin-top: 30px;
}
.veui-form-demo .left {
  float: left;
}
.veui-form-demo .right {
  float: right;
}
.veui-form-demo .output {
  position: absolute;
  display: inline-block;
  left: 560px;
  line-height: 36px;
  margin: 0 0 0 50px;
}
.veui-form-demo .output::before {
  position: absolute;
  left: -80px;
  content: "⇒";
  line-height: 32px;
  font-size: 30px;
  color: #999;
}
.veui-form-demo .two-name .veui-input {
  width: 75px;
}
.veui-form-demo .salary .veui-input {
  width: 67px;
}
.veui-form-demo .veui-uploader-list-image li {
  margin: 0;
}
.veui-form-demo .start-field .veui-field-error:first-of-type {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 80px;
  white-space: nowrap;
}
.veui-form-demo .operation {
  margin-top: 60px;
  margin-left: 120px;
}
.veui-form-demo .operation [class*="veui"] {
  margin-left: 10px;
}
.veui-form-demo .operation [class*="veui"]:first-child {
  margin-left: 0;
}



